.isOnline {
  width: 15px;
  height: 15px;
  border-radius: 100%;
}

.friendList {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.item {
  display: flex;
  gap: 20px;
  align-items: center;
  background-color: white;
  padding: 10px;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid rgb(187, 185, 185);
  width: 500px;
  border-radius: 10px;
  box-shadow: 10px 10px 8px 2px rgba(0, 0, 0, 0.3);
}

.img {
  width: 80px;
  border-radius: 10%;
  background-color: rgb(240, 235, 235);
}

.title {
  font-weight: 700;
  font-size: 20px;
  line-height: 1.5;
  color: #424242;
}
