.container {
  margin: 20px;
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: rgb(201, 200, 200);
}

.description {
  margin-bottom: 25px;
  text-align: center;
}

.profileCard {
  width: 300px;
  margin-right: auto;
  margin-left: auto;
  padding: 30px 40px;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 30px;
}

.avatar {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  background-color: rgb(180, 183, 186);
}

.name {
  font-weight: 700;
  font-size: 24px;
  line-height: 1.71;
  color: #010101;
  margin-bottom: 5px;
}

.tag {
  font-size: 16px;
  color: gray;
  margin-bottom: 5px;
}

.location {
  font-size: 16px;
  color: gray;
  margin-bottom: 15px;
}

.stats {
  margin: 0 auto;
  padding: 10px;
  width: 100%;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}

.stats li {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-basis: 33.333%;
  gap: 7px;
}

.label {
  font-weight: 500;
  color: gray;
}

.quantity {
  font-weight: 700;
  color: #010101;
}
